@import 'https://use.fontawesome.com/releases/v5.5.0/css/all.css';
@import 'https://fonts.googleapis.com/css?family=Roboto';
:root {
  --text-color: #dadada;
  --default-font-size: 1rem;
  --mobile-font-size: 0.875rem;
  --link-color: #49A0CC;
  --nav-blue: #2693E6;
  --yellow: #FFBA00;
  --dark: #111111;
  --default-box-shadow: 0px 0px 22px 2px rgba(0, 0, 0, 0.4); }

* {
  margin: 0;
  padding: 0; }

html {
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

*,
*::before,
* ::after {
  -webkit-box-sizing: inherit;
  box-sizing: inherit; }

#__bs_notify__ {
  display: none !important; }

html {
  font-family: 'Roboto', sans-serif;
  line-height: 1.5;
  font-size: var(--mobile-font-size); }

@media screen and (min-width: 500px) {
  html {
    font-size: var(--default-font-size); } }

body, table, th, td, form, input, textarea, option, select {
  font-family: inherit; }

a {
  color: var(--link-color);
  text-decoration: none; }
  a:hover {
    text-decoration: underline; }

h1, h2, h3, h4, h5, h6 {
  line-height: 1.2;
  padding-bottom: 0.5em; }

p, ul, ol {
  padding-bottom: .5rem; }

ul, ol {
  padding-left: 2rem; }

li {
  list-style-position: inside; }

header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  -webkit-box-flex: 1;
  -ms-flex: 1 0 100%;
  flex: 1 0 100%;
  position: relative; }
  header nav ul {
    padding: 0;
    display: block; }
  header nav ul li ul {
    position: absolute;
    width: 8.7rem;
    opacity: 0;
    pointer-events: none;
    -webkit-transition: opacity 0.1s ease-out, -webkit-transform 0.1s ease-in;
    transition: opacity 0.1s ease-out, -webkit-transform 0.1s ease-in;
    transition: opacity 0.1s ease-out, transform 0.1s ease-in;
    transition: opacity 0.1s ease-out, transform 0.1s ease-in, -webkit-transform 0.1s ease-in;
    -webkit-transform: translateY(2rem);
    transform: translateY(2rem); }
  header nav > ul > li > ul {
    top: 100%;
    left: 0;
    padding-top: 1rem; }
  header nav > ul > li > ul > li:not(:last-child) {
    margin-bottom: 1rem; }
  header nav li {
    display: block;
    list-style: none;
    position: relative; }
    header nav li a {
      display: block;
      padding: .7rem 1rem .7rem 1rem;
      border-radius: 3px;
      border-bottom: 2px solid rgba(0, 0, 0, 0); }
    header nav li a:hover {
      background: var(--dark);
      border-bottom: 2px solid var(--nav-blue);
      -webkit-box-shadow: var(--default-box-shadow);
      box-shadow: var(--default-box-shadow); }
  header nav li:not(:last-child) {
    margin-right: .3rem; }
  header nav a {
    color: var(--text-color);
    text-decoration: none;
    text-transform: uppercase; }
    header nav a:hover {
      text-decoration: none; }
  header nav i {
    margin-right: .3rem; }

header nav > ul {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex; }

.site-title {
  font-size: 2rem;
  font-weight: bold;
  letter-spacing: .1rem; }
  .site-title i {
    color: var(--yellow);
    margin-right: .5rem; }
  .site-title a {
    color: var(--nav-blue); }
    .site-title a:hover,
    .site-title a:hover > i {
      color: var(--text-color);
      text-decoration: none; }

#nav-toggle {
  display: none; }

@media screen and (max-width: 800px) {
  nav {
    display: none; }
    nav .first-level {
      display: block; }
    nav .first-level li ul {
      opacity: 1;
      pointer-events: inherit;
      width: auto;
      position: static;
      -webkit-transform: translate(0, 0);
      transform: translate(0, 0);
      padding: 0.5rem 0 0.5rem 1.5rem; }
    nav > ul > li:not(:last-child) {
      padding: 0 0 0.5rem 0; }
  #nav-toggle:checked + header nav {
    display: block;
    -webkit-box-flex: 1;
    -ms-flex: 1 0 100%;
    flex: 1 0 100%; }
  .fa-bars {
    cursor: pointer; }
  #nav-toggle:checked + header .fa-bars:before {
    content: '\f00d';
    cursor: pointer; } }

@media screen and (min-width: 800px) {
  #toggle {
    display: none; }
  nav li:hover > ul {
    /* display: block; */
    opacity: 1;
    pointer-events: inherit;
    -webkit-transform: translateY(0);
    transform: translateY(0); } }

body {
  background: #111111;
  color: var(--text-color);
  max-width: 65rem;
  margin: 0 auto; }

.site {
  background: #222222;
  padding: 0.7rem 2rem;
  border-radius: 3px; }
